.post-column {
  margin-bottom: 30px;
}
.post {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.post-image {
  width: 100%;
}
.post-wrapper {
  background-color: var(--white-color);
  flex-grow: 1;
  padding: 20px;
}
.post-tag {
  background-color: var(--primary-color);
  border-radius: 15px;
  color: var(--white-color);
  display: inline-block;
  font-size: 12px;
  font-weight: 700;
  line-height: 1;
  margin-bottom: 8px;
  padding: 4px 8px;
}
.post-header {
  margin-bottom: 10px;
}
.post-title {
  font-size: 18px;
  margin-bottom: 0;
}
.post-meta {
  background-color: var(--white-color);
  border-top: 1px solid var(--light-gray-color);
  color: var(--secondary-text-color);
  display: flex;
  font-size: 13px;
  font-weight: 700;
  padding: 10px 30px;
}
.post-meta-length:before {
  content: "\02022";
  font-family: serif;
  padding: 0 8px;
}
.post-date {
  color: var(--secondary-text-color);
  display: block;
  font-size: 11px;
  font-weight: 700;
  text-transform: uppercase;
}
.post-excerpt {
  color: var(--secondary-text-color);
  font-size: 14px;
}
.post-content {
  font-size: 15px;
  line-height: 1.8;
}
.post-tags {
  margin-top: 15px;
}
.post-tags .tag {
  font-weight: 700;
}
.post-tags .tag + .tag {
  margin-left: 10px;
}
.post-tags .tag:before {
  content: "#";
}
.post-footer {
  align-items: center;
  background-color: var(--white-color);
  border-top: 1px solid var(--light-gray-color);
  display: flex;
  height: 42px;
  justify-content: space-between;
  padding: 0 20px;
}
.post-link {
  font-size: 11px;
  font-weight: 700;
  letter-spacing: 0.5px;
  line-height: 1;
  text-transform: uppercase;
}
.post-author {
  display: flex;
  margin-right: 2px;
}
.post-author-link {
  display: block;
  height: 28px;
  margin: 0 -4px;
  position: relative;
  width: 28px;
}
.post-author-link:first-child {
  z-index: 10;
}
.post-author-link:nth-child(2) {
  z-index: 9;
}
.post-author-link:nth-child(3) {
  z-index: 8;
}
.post-author-link:nth-child(4) {
  z-index: 7;
}
.post-author-link:nth-child(5) {
  z-index: 6;
}
.post-author-image {
  border: 2px solid var(--white-color);
  border-radius: 50%;
  height: 100%;
  object-fit: cover;
  width: 100%;
}
